:root {
	--bg-color:		 		#fafafa;
	--bg-color2:			#fafafa;
	--bg-link:				#fcd535;
	--bg-danger:			#f9e8ea;
	--bg-success:			#e2f5ee;
	--bg-gray:				#eaecef;
	--border:				#eef0f2;
	--text:		 			#1e2329;
	--link:					#c99400;
	--gray:					#76808F;
	--danger:				#cf304a;
	--success:				#03a66d;
	--danger2:				#f6465d1a;
	--success2:				#0ecb811a;
	
	--dark-bg-color:		#161a1e;
	--dark-bg-color2:		#1e2026;
	--dark-bg-link:			#fcd535;
	--dark-bg-danger:		#2c1e24;
	--dark-bg-success:		#152b28;
	--dark-bg-gray:			#2b3139;
	--dark-border:			#252930;
	--dark-text:			#e4e2e8;
	--dark-link:			#f0b90b;
	--dark-gray:			#76808F;
	--dark-danger:			#cf304a;
	--dark-success:			#03a66d;
	--dark-danger2:			#f6465d1a;
	--dark-success2:		#0ecb811a;

	--container-width:		1400px;

	--sans-serif:		 	Arial, Helvetica, sans-serif; // "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
	--monospace: 			SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
}

.dark {
	--bg-color:		 		var(--dark-bg-color);
	--bg-color2:			var(--dark-bg-color2);
	--bg-link:				var(--dark-bg-link);
	--bg-danger:			var(--dark-bg-danger);
	--bg-success:			var(--dark-bg-success);
	--bg-gray:				var(--dark-bg-gray);
	--border:				var(--dark-border);
	--text:		 			var(--dark-text);
	--text-custom:			var(--dark-text-custom);
	--link:					var(--dark-link);
	--gray:					var(--dark-gray);
	--danger:				var(--dark-danger);
	--success:				var(--dark-success);
	--danger2:				var(--dark-danger2);
	--success2:				var(--dark-success2);
}

* {
	box-sizing: border-box;
}

.scroll {
	overflow-y: auto;

	&::-webkit-scrollbar {
		position: absolute;
		width: 5px;
		height: 5px;
	}

	&::-webkit-scrollbar-track {
		// background: #ddd;
		background-color: var(--bg-gray);
	}

	&::-webkit-scrollbar-thumb {
		background: #aaa;
		border-radius: 5px;
	}

	&::-webkit-scrollbar-thumb:hover {
		background: #888;
	}
}

html, body {
	min-height: 100%;
}

body {
	margin: 0;
	font-family: var(--sans-serif);
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-size: 12px;
}
// button, a {
	// font-size: 1em;
	// font-family: var(--sans-serif);
// }

h1, h2, h3 {
	padding: 0;
	margin: 0;
}

h1, .h1 {
	font-size: 20px;
	font-weight: bolder;
}

h2, .h2 {
	font-size: 16px;
	font-weight: 400;
}

h3, .h3 {
	font-size: 13.3333px;
	font-weight: 400;
}

.small {
	font-size: 10px;
	font-weight: 400;
}

p {padding: 0; margin: 0;}
a, button, input, textarea, select {
	display: inline-block;
	text-decoration: none;
	outline: 0;
	border: 0;
	border-radius: 5px;;
	color: var(--color);
}

a, button {
	text-decoration: none;
	padding: 0;
	margin: 0;
	cursor: pointer;
}

a {
	text-decoration: none;
	color: var(--color-link);
	&.active, &:active {color: var(--link)}
	&:hover {color: var(--link);}
}

ul {
	margin: 0;
	padding: 0;
}

li, ol { list-style: none;}

.mono {
	font-family: var(--monospace);
}

.container {
	display: block;
	max-width: var(--container-width);
	margin: auto;
}

.d {
	display: flex;

	
}
.d-column {
	display: flex;
	flex-direction: column;
}
.center {
	justify-content: center;
}
.middle {
	align-items: center;
}
.around {
	justify-content: space-around;
}
.between {
	justify-content: space-between;
}
.flex-end {
	justify-content: flex-end;
}
.fill {
	flex: 1;
}
.gap {gap: 0.5em;}
.gap-1 {gap: 1em;}
.gap-2 {gap: 2em;}
.gap-3 {gap: 3em;}
.gap-4 {gap: 4em;}
.gap-5 {gap: 5em;}

.full {width: 100%;}
.col-1  {flex: 0 0 8.33%; 	width: 8.33%;}
.col-2  {flex: 0 0 16.66%; 	width: 16.66%;}
.col-3  {flex: 0 0 25%; 		width: 25%;}
.col-4  {flex: 0 0 33.33%; 	width: 33.33%;}
.col-5  {flex: 0 0 41.66%; 	width: 41.66%;}
.col-6  {flex: 0 0 50%; 		width: 50%;}
.col-7  {flex: 0 0 58.33%; 	width: 58.33%;}
.col-8  {flex: 0 0 66.66; 	width: 66.66%;}
.col-9  {flex: 0 0 75%; 		width: 75%;}
.col-10 {flex: 0 0 83.33%; 	width: 83.33%;}
.col-11 {flex: 0 0 91.66%; 	width: 91.66%;}
.col-12 {flex: 0 0 100%; 	width: 100%;}

.ln-br {white-space: nowrap;}

@media (max-width: 768px) {
	.col-1, .col-2, .col-3, .col-4, .col-6, .col-8, .col-9, .col-10, .col-11 {
		flex: 0 0 100% !important;
		width: 100%;
	}
}

.overlay {
	position: absolute;
	left: 0;
	top: 0;
	// right: 0;
	// bottom: 0;
	width: 100%;
	height: 100%;

	display: flex;
	justify-content: center;
	align-items: center;
	&.blur {
		// background-color: #fff5;
		backdrop-filter: blur(2px);
	}
}

.gray {
	color: var(--gray);
}

.link {
	color: var(--link);
}
.danger {
	color: var(--danger);
}
.success {
	color: var(--success);
}
.bold {
	font-weight: 450;
}
.bg-link {
	background-color: var(--bg-danger);
}
.cmd {
	cursor: pointer;
	&:hover {
		color: var(--link);
	}
}
.wrap {
	display: flex;
	@media (max-width: 1220px) {
		flex-wrap: wrap;
	}
}

@media (max-width: 768px) {
	.sm-hide {display: none !important;}
}
@media (min-width: 768px) {
	.sm-show {display: none !important;}
}

.btn {
    cursor: pointer;
    outline: none;
    padding: 0.5em 1em;
    border: none;
    border-radius: 4px;
	font-weight: 600;

	&:hover {opacity: 0.8;}
	&:active {
		opacity: 1;
		background: linear-gradient(to right, #888 0, #888 100%);
		background-blend-mode: multiply;
	}

	&.success {
		background-color: var(--success);
		color: white;
	}
	&.danger {
		background-color: var(--danger);
		color: white;
	}
}