.trade {
	padding: 1px;
	display: grid;
	gap: 1px;
	background-color: var(--border);
	grid-template:
		"info info market" 60px 
		"orderbook chart market" 36.4em 
		"orderbook chart trades" 5.4em
		"orderbook action trades" 30.4em
		"history history history" 20em 
		/ 300px auto 300px;

	@media (max-width: 1220px) {
		grid-template:
			"info info info" 64px
			"chart chart action" 35em
			"orderbook trades action" 14.4em
			"orderbook trades market" 22em
			"orderbook trades market" 35.4em
			"history history history" 300px
			/ 30% 30% 40% ;
	}

	@media (max-width: 768px) {
		grid-template:
			"info" 150px
			"chart" 405px
			"action" 590px 
			"market" 400px
			"orderbook" 71.8em
			"trades" 200px
			"history" 200px
			/ 100% ;
	}

	> section {
		background-color: var(--bg-color);
	}

	
	.info {
		grid-area: info;

		display: flex;
		flex-wrap: wrap;
		align-items: center;
		gap: 1em;
		padding: 0.4em;
		ul {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			gap: 2em;
			> :first-child {
				padding-left: 2em;
				padding-right: 1em;
				border-right: 1px solid var(--border);
			}
			div, h2 {
				line-height: 1.2rem;
			}
		}
	}

	.orderbook {
		grid-area: orderbook;
		padding: 0.3em 0;
		> div {
			padding: 0.5em;
		}
		
		ul {
			height: calc(32.4em);
		}
		
		li {
			padding: 0 0.5em;
			:nth-child(1) {
				font-weight: 600;
			}
		}
		li, .list {
			line-height: 1.8em;
			width: 100%;
			display: flex;
			align-items: center;
			:nth-child(1) {
				width: 30%;
			}
			:nth-child(2) {
				width: 30%;
				text-align: right;
			}
			:nth-child(3) {
				width: 40%;
				text-align: right;
			}
		}
		.sell {
			display: flex;
			flex-direction: column-reverse;
			li {
				background: linear-gradient(to bottom, var(--danger2) 0, var(--danger2) 100%) no-repeat;
				background-position: right;
				div:nth-child(1) {
					color: var(--danger);
				}
			}
		}
		.buy {
			li {
				background: linear-gradient(to bottom, var(--success2) 0, var(--success2) 100%) no-repeat;
				background-position: right;
				div:nth-child(1) {
					color: var(--success);
				}
			}
		}
	}
	.chart {
		grid-area: chart;
		.period {
			display: flex;
			height: 100%;
			
			li {
				display: flex;
				align-items: center;
				border-bottom: 1px solid var(--border);
			}
			span {
				display: block;
				padding: 0.5em 0.5em;
	
				&.active {
					color: var(--link);
				}
			}
		}
		.info {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			display: flex;
			z-index: 1;
		}
	}
	
	.action {
		grid-area: action;
		position: relative;
		background-color: var(--bg-color2);
		ul {
			display: flex;
		}
		
		.content {
			
			// height: 100%;
		}

		.tab {
			li {
				border-bottom: 1px solid var(--border);
				background-color: var(--bg-color);
				span {
					cursor: pointer;
					display: block;
					color: var(--gray);
					
					padding: 1em 2em;
				}
				&.active {
					border-right: 1px solid var(--border);
					span {
						color: var(--text);
						background-color: var(--bg-color2);
						border-top: 2px solid var(--bg-link);
						margin-bottom: -1px;
						font-weight: 550;
					}
				}
			}
		}
		.range-slider {
			width: 100%;
			position: relative;
			margin-bottom: 15px;
			.text-region {
				position: absolute;
				width: 100%;
				top: 22px;
				left: 0;
			}
		  }
		  
		  .range-slider__range {
			  -webkit-appearance: none;
			  width: 100%;
			  height: 5px;
			  border-radius: 5px;
			  background: var(--bg-gray);
			  outline: none;
			  padding: 0;
			  margin: 0;
		  
			  &::-webkit-slider-thumb {
				  appearance: none;
				  width: 18px;
				  height: 18px;
				  border-radius: 50%;
				  background: var(--gray);
				  cursor: pointer;
				  transition: background .15s ease-in-out;
			  
				  &:hover {
				  background: var(--gray);
				  }
			}
		  
			  &:active::-webkit-slider-thumb {
				  background: var(--gray);
				  opacity: 0.8;
			  }
			  
			  &::-moz-range-thumb {
				  width: 20px;
				  height: 20px;
				  border: 0;
				  border-radius: 50%;
				  background: var(--gray);
				  cursor: pointer;
				  transition: background .15s ease-in-out;
			  
				  &:hover {
				  background: var(--gray);
				  }
			  }
			  
			  &:active::-moz-range-thumb {
				  background: var(--gray);
			  }
		  }
		  
		  .range-slider__value {
			display: inline-block;
			position: relative;
			width: 60px;
			color: var(--bg-color);
			line-height: 20px;
			text-align: center;
			border-radius: 3px;
			background: var(--text);
			padding: 5px 10px;
			margin-left: 8px;
		  
			  &:after {
				  position: absolute;
				  top: 8px;
				  left: -7px;
				  width: 0;
				  height: 0;
				  border-top: 7px solid transparent;
				  border-right: 7px solid var(--text);
				  border-bottom: 7px solid transparent;
				  content: '';
			  }
		  }
		  
		  ::-moz-range-track {
			  background: var(--gray);
			  border: 0;
		  }
		  
		  input::-moz-focus-inner,
		  input::-moz-focus-outer { 
			border: 0; 
		  }
	}
	.market {
		grid-area: market;
		.market-input {
			margin: 0.8em;
			padding-top: 0.3em;
			padding-bottom: 0.3em;
		}
		.slider {
			li {
				span {
					width: auto !important;
				}
				&:hover {
					background-color: transparent;
				}
			}
		}
		li, .list {
			line-height: 1.8em;
			width: 100%;
			display: flex;
			align-items: center;
			padding: 0 0.8em;
			cursor: pointer;
			&:hover {
				background-color: var(--bg-gray);
			}
			> :nth-child(1) {
				width: 40%;
			}
			> :nth-child(2) {
				width: 30%;
				text-align: right;
			}
			> :nth-child(3) {
				width: 30%;
				text-align: right;
			}
		}
	}
	.tradelist {
		grid-area: trades;
		li, .list {
			line-height: 1.8em;
			width: 100%;
			display: flex;
			align-items: center;
			padding: 0 0.8em;
			cursor: pointer;
			> :nth-child(1) {
				width: 40%;
			}
			> :nth-child(2) {
				width: 30%;
				text-align: right;
			}
			> :nth-child(3) {
				width: 30%;
				text-align: right;
			}
		}
	}
	
	.history {
		grid-area: history;
	}
}