.input {
    display: flex;
    align-items: center;
    background-color: var(--bg-gray);
    border: 1px solid transparent;
    padding: 0.5em;
    gap: 0.5em;
    border-radius: 0.25em;
    input {
        flex: 1;
        background-color: transparent;
        outline: none;
    }
    &:hover, &.active {
        border: 1px solid var(--bg-link);
    }
}