.layout {
    color: var(--text);
    background-color: var(--bg-color);
    min-height: 100vh;

    header {
        background-color: var(--bg-color);
        border-bottom: 1px solid var(--border);
        position: sticky;
        z-index: 10;
        padding: 0 1em;
        top: 0;
        width: 100%;
        user-select: none;
        .container > div {
            height: 4em;
        }
        button {
            color: var(--text);
            cursor: pointer;
            background-color: transparent;

            &:hover {
                color: var(--link);
            }
        }
    }
    main {
        min-height: calc(100vh - 5em);
        margin-top: -1px;
        margin-bottom: -1px;
    }
    footer {
        height: 2em;
        background-color: var(--bg-color);
        border-top: 1px solid var(--border);
        color: var(--text);
        display: flex;
        position: sticky;
        gap: 1em;
        bottom: 0;
        z-index: 10;
        justify-content: center;
        align-items: center;
    }
}