.connectbutton {
	.connect-dlg {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(33, 4, 65, 0.7);
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		z-index: 1001;
	
		&.hidden {
			display: none;
		}
		@media (max-width: 576px) {
			li {
				padding: 7px !important;
			}
		}
		ul {
			border: 0;
			border-top: 3px solid #F528EC;
			border-radius: 4px;
			width: 450px;
			height: auto;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			background-color: white;
			border-bottom: 3px solid white;
			@media (max-width: 576px) {
				width: 350px;   
			}
			.title {
				display: block;
				font-weight: 300;
				font-size: 24px;
				padding: 20px;
				text-align: center;
				text-transform: uppercase;
				background: linear-gradient(270deg, #47DDF1 0%, #5A2AE1 52.08%, #560099 100%);
				-webkit-background-clip: text;
				-webkit-text-fill-color: transparent;
				background-clip: text;
			}
			li:not(.title) {
				width: 100%;
				background: linear-gradient(228.18deg, #F2F4FF 13.02%, #FFFFFF 76.16%);
				display: flex;
				flex-direction: row;
				justify-content: space-around;
				align-items: center;
				padding: 20px;
				cursor: pointer;
				&:hover {
					background: linear-gradient(228.18deg, #e6e7f2 13.02%, #f0f0f0 76.16%);
				}
				.img-region {
					width: 30%;
					display: flex;
					justify-content: center;
					align-items: center;
				}
				.text-region {
					width: 70%;
					display: flex;
					flex-direction: column;
				}
				.big {
					font-weight: 600;
					font-size: 19px;
					line-height: 25px;
					color: #2B1953;
					margin: 5px;
				}
				.detail {
					font-weight: 400;
					font-size: 15px;
					line-height: 17px;
					color: #2B1953;
					margin: 5px;
				}
			}
		}
		.error-region {
			width: 800px;
			height: 200px;
			background-color: white;
			border-radius: 10px;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			@media (max-width: 768px) {
				width: 85%;
				padding: 10px;
			}
	
			.error-text {
				margin: 0;
				padding: 0 !important;
				line-break: anywhere;
				&.big {
					color: red;
					font-size: 30px;
					font-weight: 600;
					padding-bottom: 20px !important;
				}
				color: black;
				font-size: 25px;
				padding: 40px 60px;
			}
		}
		
		
	}

	.con-btn {
		// font-size: 12px;
		color: var(--purple);
		
		padding: 8px;
		width: 145px;
		border-radius: 3px;
		text-transform: uppercase;
		background-color: transparent;
		&:hover {
			background-color: rgba(255, 255, 255, 0.2);
		}
		&:active {
			background-color: rgba(255, 255, 255, 0.3);
		}
		&.text-purple {
			&:hover {
				background-color: rgba(170, 170, 170, 0.1);
			}
			&:active {
				background-color: rgba(170, 170, 170, 0.2);
			}
		}
		&.disconnect {
			border: 1px solid #8CD91D;
		}
		&.connect {
			border: 1px solid #F528EC;
		}
	}
	.con-circle {
		background-color: #8CD91D;
		border: 0;
		border-radius: 50%;
		width: 8px;
		height: 8px;
	}
	&.dark {
		.con-btn {
			// font-size: 12px;
			color: white;
		}
	}
}

